body{
  background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
}

/* The section of the body App */
.App{
  background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
  height:650px;
  width:25%;
  margin:40px auto;
  padding:20px;

}


/* The section of the header */
header{
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
  height:120px;
  width:100%;
  border-radius:5px;
  border:1px solid white;
}
header h1{
  color:white;
  font-family:monospace;
  text-align:center;
  font-size:35px;
}
header p{
  color:white;
  text-align:center;
  font-family:cursive;
}


/* The section of the main */
main{
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
  height: 405px;
  width:100%;
  border-radius:5px;
  margin-top:20px;
  border:1px solid white;
  overflow:auto;
}
main::-webkit-scrollbar{
  display:none;
}

marquee{
  color:orangered;
  font-family:cursive;
  font-weight:normal;
  font-size:12px;
  padding:0;
  text-shadow:1px 1px white;
}

/* The section of the footer */
footer{
  height:70px;
  width:100%;
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
  margin-top:10px;
  border-radius:5px;
  border:1px solid white;
}
footer input{
  background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
  padding:12px;
  width:61%;
  border:none;
  outline:none;
  margin:12px 10px;
  font-family:cursive;
  border-radius:25px 0 0 0;
  font-size:15px;
  border-top:1px solid yellow;
  border-left:1px solid yellow;
  border-bottom:1px solid yellow;
  color:white;
}
footer input::placeholder{
  font-family:cursive;
  font-size:14px;
  color:white;
}

footer button{
  background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
  padding:12px;
  width:30%;
  border:none;
  outline:none;
  margin-left:-20px;
  font-family:cursive;
  border-radius:0 0 25px 0;
  color:white;
  font-weight:bold;
  font-size:15px;
  border-top:1px solid yellow;
  border-right:1px solid yellow;
  border-bottom:1px solid yellow;
  cursor:pointer;
}
footer button:hover{
  background:orange;
}